@import url(https://fonts.googleapis.com/css?family=Just+Another+Hand&display=swap);
body {
  margin: 0;
  font-family: 'Just Another Hand', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.header_main
{
  font-size: 200%;
}
.header_ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: rgb(0, 80, 117);
  }
  
  .header_li {
    float: left;
  }
  
  .header_li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
  .header_li a:hover   {
    background-color: rgb(6, 117, 168);
  }
  .header_main a.active{
    background-color: rgb(6, 117, 168);

  }

  
  .header_anchorIcon
  {
    position: absolute;
    /* margin:1%; */
    right: 1%;
    top:1%;
  }

  .dropdown-content {
    display: none;
    position: fixed;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  
  .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
  }
  
  .dropdown-content a:hover {background-color: navy;}
  
  .dropdown:hover .dropdown-content {
    display: block;
  }
.portfolio_Header
{
    width: 100%;
    text-align:center;
    font-size: 200%;;
}
.portfolio_Header select
{
    font-size: 100%;
    width: 10%;
    text-align:center;
}
.portfolio_classTable
{
    width: 30%;
    position: absolute;
}
.portfolio_classTable_btn
{
    font-size: 100%;;
    margin-left: 3%;
}
.portfolio_searchControlDiv
{
    margin-left: 3%;
    margin-top: 3%;
}
.classesTable
{
    border:1px solid navy;
    width: 30%;
    margin-left: 3%;
    margin-top:1%;
}

.classesTable th{
    text-align: center;
    background-color: rgb(0, 94, 138);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

